@import url(https://fonts.googleapis.com/css2?family=Jua&family=Nanum+Myeongjo:wght@700&family=Noto+Sans+KR:wght@300&family=Noto+Serif+KR:wght@900&display=swap);
.result_title {
  display: inline;
  font-size: 40px;
  background-color: rgba(255, 200, 117, 0.432);
  font-family: var(--result-font);
  color: rgb(66, 66, 66);
}
.result_subtitle {
  display: inline;
  background-color: rgba(255, 201, 121, 0.473);
  font-size: 20px;
  color: rgb(65, 65, 65);
}
.result_img {
  margin-bottom: 0.5em;
}
.result_personal {
  height: 19em;
  width: 21em;
  border-radius: 20px;
  background-color: white;
  margin-bottom: 1.5em;
  text-align: left;
  padding-right: 1em;
  padding-top: 1.5em;
  padding-bottom: 1.5em;
}
.result_personal_font {
  font-size: 13px;
}
.result_personal_line {
  padding-bottom: 1.5em;
}
.result_best {
  margin-right: 0.7em;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 1em;
  width: 8em;
  height: 11.7em;
  background-color: white;
  display: inline-block;
  border-radius: 20px;
}
.result_best_title {
  width: 8em;
  height: 1.7em;
  background-color: rgb(253, 146, 125);
  border-radius: 5px;
  padding-top: 1px;

  font-family: var(--content-font);
}
.result_font {
  padding-top: 3px;
  font-size: 14px;
  color: white;
}
.result_worst {
  margin-left: 0.7em;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 1em;
  width: 8em;
  height: 11.7em;
  background-color: white;
  display: inline-block;
  border-radius: 20px;
}
.result_worst_title {
  width: 8em;
  height: 1.7em;
  background-color: rgb(41, 134, 195);
  border-radius: 5px;
  padding-top: 1px;
  font-family: var(--content-font);
}
.result_explain {
  padding-top: 1.5em;
  padding-left: 1.5em;
  padding-right: 1.5em;

  font-family: var(--content-font);
  border: 2px;
  color: rgb(166, 166, 166);
  width: 22em;
  height: 55em;
  background-color: rgb(255, 246, 230);
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
  margin-bottom: 3em;
}

.result_list {
  text-align: left;
  font-family: var(--content-font);
  font-size: 1.2em;
}

.link_copy {
  padding-top: 0.7em;
  display: block;
  height: 2em;
  width: 11.5em;
  background-color: rgb(255, 235, 202);
  margin-left: auto;
  margin-right: auto;
}
.restart {
  padding-top: 0.7em;
  display: block;
  height: 2em;
  width: 11.5em;
  background-color: rgb(255, 235, 202);
  margin-top: 1em;
  margin-left: auto;
  margin-right: auto;
  -webkit-text-decoration: underline rgb(255, 235, 202);
          text-decoration: underline rgb(255, 235, 202);
  color: black;
}
.result_padding {
  padding-left: 1.5em;
}
.result_padding_bottom {
  text-align: left;
  padding-bottom: 1em;
}

:root {
  --bg-color: rgb(255, 255, 255);
  --font-color: black;
  --title-font: "Nanum Myeongjo", serif;
  --content-font: "Noto Sans KR", sans-serif;
  --result-font: "Jua", sans-serif;
}

body {
  height: auto;
  display: block;
  background-color: rgb(255, 255, 255);
  background-color: var(--bg-color);
  text-align: center;
  justify-content: center;
  font-family: "Noto Sans KR", sans-serif;
  font-family: var(--content-font);
  font-weight: bold;
}
.main {
  height: 30em;
  width: 25em;
  background-color: rgb(255, 246, 230);
  margin-top: 5em;
  margin-left: auto;
  margin-right: auto;
  color: rgb(48, 48, 48);
}

.main_title {
  padding-top: 4em;
  font-size: 40px;
}
.main_subtitle {
  font-size: 15px;
  padding-bottom: 4em;
}
.main_img {
  margin: 0;
  max-width: 550px;
  width: 100%;
  height: auto;
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  cursor: pointer;
}
.button {
  padding: 1px 6px;
  border-width: 1px;
  border-style: outset;
  background-color: rgb(233, 233, 233);
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: none;
  border-image: initial;
}
.option-btn {
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 5em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.option-rbtn {
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 11em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.question {
  margin: 3rem 3rem 2rem;
  color: black;
  color: var(--font-color);
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  white-space: pre-line;
  display: block;
  font-size: 2em;
  -webkit-margin-before: 1.67em;
          margin-block-start: 1.67em;
  -webkit-margin-after: 1.67em;
          margin-block-end: 1.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}
.num {
  font-size: 40px;
  margin-bottom: 0.5em;
}
.start_button {
  padding: 1em 6em 1em 6em;
  font-size: 15px;
  font-weight: bold;
  background-color: rgba(255, 200, 117, 0.432);
  display: inline-block;
}

